  position: relative;
  border-radius: 0.75em;
  cursor: pointer;
  display: block;
  width: 3em;
  height: 1.5em;
  transition: background-color 0.4s cubic-bezier(0.65, 0, 0.35, 1);
  -webkit-appearance: none;
  appearance: none;
  border: none;
  outline: none;
}
.switch__input:before {
  border-radius: 50%;
  content: "";
  display: block;
  position: absolute;
  top: 0.125em;
  left: 0.125em;
  width: 1.25em;
  height: 1.25em;
  transition: background-color 0.4s cubic-bezier(0.65, 0, 0.35, 1),
        transform 0.4s cubic-bezier(0.65, 0, 0.35, 1);
}
.switch__icon,
.switch__icon-part {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
.switch__icon {
  background-color: var(--bg-primary);
  border-radius: 50%;
  overflow: hidden;
  pointer-events: none;
  top: 0.125em;
  left: 0.125em;
  width: 1.25em;
  height: 1.25em;
  transition: transform 0.4s cubic-bezier(0.65, 0, 0.35, 1);
}
