  background-color: var(--bg-primary);
  top: calc(50% - 0.375em);
  left: calc(50% - 0.375em);
  width: 0.75em;
  height: 0.75em;
}
.switch__icon-part--2 {
  background-color: var(--text-primary);
  top: calc(50% - 0.4375em);
  left: calc(50% - 0.0625em);
