.switch__icon-part--5,
.switch__icon-part--6,
.switch__icon-part--7,
.switch__icon-part--8,
.switch__icon-part--9,
.switch__icon-part--10,
.switch__icon-part--11 {
  transition: transform 0.4s cubic-bezier(0.65, 0, 0.35, 1);
